body {
	margin: 0;
	padding: 0;
	font-family: helvetica, arial, sans-serif;
}

h5 {
	font-weight: normal;
}

.fold {
	width: 1160px;
	margin: auto;
}

div#header {
	width: 100%;
	height: 80px;
	margin-bottom: 24px;
	border-bottom: 1px solid #eee;
}

	div#header h1 {
		margin: 0;
		padding: 26px 24px 0;
		font-size: 1.500em;
	}

		div#header h1 a {
			text-decoration: none;
			color: #302c4c;
		}

div.post {
	width: 100%;
}

	div.post img {
		margin: 24px;
	}

	div.post h2 {
		margin: 0 24px 8px;
		padding: 0;
		color: #302c4c;
	}

		div.post h2 a {
			font-size: 1em;
			color: #cc294c;
			text-decoration: none;
		}

	div.post h5 {
		font-size: 0.875em;
		margin: 0 24px 8px;
		padding: 0;
		color: #97a2b2;
	}

	div.post p {
		margin: 0 24px 24px;
		font-size: 1em;
		line-height: 1.375em;
		color: #302c4c;
	}

		div.post p a {
			color: #cc294c;
		}

div#copyright {
	width: 100%;
}
	
	div#copyright p {
		margin: 24px;
		font-size: 0.875em;
		color: #97a2b2;
	}

		div#copyright p a {
			color: #97a2b2;
		}

@media only screen and (max-width : 1160px) {

	div.fold {
		width: 100%;
	}

	div.post img {
		width: 90%;
	}

}